@use 'https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;700&family=Work+Sans:wght@200;300;400;500&display=swap';

$text-color: #FFFFFF;
$dark: #22252C;
$posy-rose: #813772;
$ink-blue: #062F4F;
$text-font: 'Work Sans', sans-serif;
$title-font: 'Montserrat', sans-serif;

.page-home {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.home-image {
  width: 50%;
  object-fit: cover;
  object-position: center;
}

.home-title {
  font-family: $title-font;
  font-weight: bolder;
  font-size: 4rem;
}

.page-about {
  width: 100%;
  height: 100%;
  display: flex;
}

.about-info {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.info-title {
  color: $posy-rose;
  font-weight: 400;
  font-size: 1.4rem;
  text-align: center;
}

.info-text {
  padding: 2rem 1rem;
  font-weight: 200;
  line-height: 1.5rem;
  margin: 1rem 0;
  border: 2px solid $ink-blue;
  border-radius: 5px;
}

.info-skills {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1rem;
  border: 2px solid $ink-blue;
  border-radius: 5px;
}

.info-skills ul {
  width: min-content;
  padding: 2rem 0;
  list-style: none;
}

.info-skills ul:first-child {
  padding-right: 4rem;
}

.skills-item {
  display: flex;
  align-items: center;
  color: $dark;
  font-size: 0.9rem;
  padding-bottom: 0.8rem;
}

#item-icon {
  color: $dark;
  font-size: 1.3rem;
  font-weight: 500;
  margin-right: 0.4rem;
}

.about-timeline {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.timeline {
  display: flex;
  flex-direction: column;
  justify-content: center !important;
}

.timeline-date,
.timeline-info {
  font-family: $text-font !important;
}

.timeline-date {
  color: $ink-blue;
  font-weight: 500 !important;
}

.timeline-info {
  color: $dark;
  font-weight: 200 !important;
}

.timeline-dot {
  background-color: $posy-rose !important;
}

.page-projects {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.projects-list_left,
.projects-list_right {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.projects-list_left {
  border-right: 2px solid $posy-rose;
}

.project-info {
  height: 20rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.project-image {
  height: 20rem;
  padding-bottom: 3rem;
  object-fit: cover;
  object-position: center;
  overflow: hidden;
}

.project-title {
  color: $ink-blue;
  font-size: 1.5rem;
  text-align: center;
  padding: 0 0 0.5rem;
  font-weight: 500;
}

.project-tech {
  text-align: center;
  padding-top: 0.5rem;
}

.project-link__container {
  width: 100%;
  display: flex;
  justify-content: space-around;
  position: absolute;
  bottom: 0;
  padding-bottom: 3rem;
}

.project-link {
  color: $dark;
  text-decoration: none;
  font-size: 1.2rem;
}

// 750px flex column!
